<template>
  <div class="employeesManagement">
    <div class="mid">
      <div class="list mescroll" id="list">
        <div id="listData">
          <div class="item" v-for="(item,index) in list" :key="index">
            <van-swipe-cell :disabled="item.etype==5?true:false">
              <div class="info">
                <img :src="$utils.dealImg(item.userHeader)" alt />
                <div class="right">
                  <div class="name">{{item.userName}}</div>
                  <div class="price">
                    {{getType(item.etype)}}
                  </div>
                </div>
              </div>
              <template slot="right">
                <van-button square type="primary" text="编辑" @click="edit(item,index)" />
                <van-button square type="danger" text="删除" @click="del(item,index)" />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom" @click="qrCodeShow = true">新增员工</div>
    <van-popup v-model="qrCodeShow">
      <img class="qrcode" :src="qrcode" alt="">
    </van-popup>
    <van-popup v-model="show">
      <div class="popup">
        <div class="popup-mid">
          <p class="title">修改员工信息</p>
          <div class="info">
            <img :src="$utils.dealImg(detail.userHeader)" alt="" />
            <p>{{detail.userName}}</p>
          </div>
          <van-cell-group>
            <van-cell title="员工职位" :value="getType(detail.etype) || '请选择'" is-link @click="typeShow=true" />
          </van-cell-group>
        </div>
        <div class="popup-bottom">
          <div class="btn" @click="subsmit">提交</div>
        </div>
      </div>
    </van-popup>
    <van-popup v-model="typeShow" position="bottom">
      <van-picker :columns="columns" value-key="text" show-toolbar @cancel="typeShow = false" @confirm="typeConfirm" />
    </van-popup>
  </div>
</template>
<script>
import {
  SwipeCell,
  Button,
  Popup,
  CellGroup,
  Cell,
  Dialog,
  Picker,
} from "vant";
import {
  getEmployeesList,
  delEmployees,
  updateEmployees,
  recommend,
} from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [SwipeCell.name]: SwipeCell,
    [Button.name]: Button,
    [Popup.name]: Popup,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Picker.name]: Picker,
  },
  data() {
    return {
      show: false,
      detail: {},
      list: [],
      qrCodeShow: false,
      mescroll: null,
      qrcode: "",
      typeShow: false,
      columns: [
        {
          text: "营业员",
          value: 0,
        },
        {
          text: "送货员",
          value: 1,
        },
        {
          text: "店长",
          value: 2,
        },
        {
          text: "会计",
          value: 3,
        },
      ],
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onChange() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    edit(item) {
      this.show = true;
      this.detail = Object.assign({}, item);
    },
    typeConfirm(item, index) {
      console.log(item);
      this.detail.etype = item.value;
      this.typeShow = false;
    },
    subsmit() {
      updateEmployees({
        ecode: this.detail.ecode,
        etype: this.detail.etype,
      }).then(res => {
        if (res.code == 0) {
          this.show = false;
          this.$toast("修改成功");
          this.mescroll.resetUpScroll();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    del(item, index) {
      let that = this;
      Dialog.confirm({
        title: "提示",
        message: "是否确认删除此员工？",
      })
        .then(() => {
          // on confirm
          delEmployees({ ecode: item.ecode }).then(res => {
            if (res.code == 0) {
              that.$toast("删除成功");
              that.mescroll.resetUpScroll();
            } else {
              that.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    getQrcode() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      recommend({
        url:
          this.$config.host2 +
          "/wx/#/bindResult?shopCode=" +
          sessionStorage.getItem("shopCode"),
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.qrcode = res.data;
        } else {
          this.$toast.fail(res.errorMsg);
        }
      });
    },
    getType(type) {
      let typeText = "";
      switch (type) {
        case 0:
          typeText = "送货员";
          break;
        case 1:
          typeText = "营业员";
          break;
        case 2:
          typeText = "店长";
          break;
        case 3:
          typeText = "会计";
          break;
        case 5:
          typeText = "店老板";
          break;
        default:
          typeText = "";
          break;
      }
      return typeText;
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
      };
      getEmployeesList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据",
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true, // 是否开启懒加载,默认false
          },
        },
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    },
  },
  mounted() {
    document.title = "员工管理";
    this.startMescroll();
    this.getQrcode();
  },
};
</script>
<style lang="scss" scoped>
.employeesManagement {
  .mid {
    height: calc(100vh - 50px);
    .list {
      .item {
        background: #fff;
        border-bottom: 1pt solid #efefef;
        .info {
          padding: 10px;
          display: flex;
          align-items: center;
          img {
            width: 70px;
            height: 70px;
            border-right: 5px;
            margin-right: 10px;
            border-radius: 10px;
          }
          .right {
            flex: 2;
            .name {
              font-weight: bold;
              line-height: 30px;
            }
            .price {
              span {
                color: #ee0a24;
                margin-right: 5px;
              }
              del {
                color: #999;
                font-size: 13px;
              }
            }
          }
        }
        .van-button {
          height: 100%;
        }
      }
    }
  }
  .bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 50px;
    line-height: 50px;
    background: $base-color;
    color: #fff;
    text-align: center;
  }
  .van-popup {
    border-radius: 5px;
  }
  .qrcode {
    width: 80vw;
  }
  .popup {
    background: #fff;
    width: 85vw;
    height: 85vh;
    position: relative;
    &-mid {
      .title {
        line-height: 50px;
        text-align: center;
      }
      .info {
        text-align: center;
        img {
          width: 100px;
          border-radius: 10px;
        }
        p {
          line-height: 30px;
        }
      }

      .van-cell {
        padding: 10px;
      }
    }
    &-bottom {
      padding: 10px;
      position: absolute;
      width: calc(100% - 20px);
      height: 45px;
      bottom: 0;
      left: 0;
      .btn {
        width: 100%;
        line-height: 45px;
        text-align: center;
        background: #ee0a24;
        color: #fff;
        font-size: 13px;
        border-radius: 5px;
      }
    }
  }
}
</style>
<style lang="scss">
.employeesManagement {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
}
</style>
